import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { filter, take } from 'rxjs/operators';
import { IResRefreshToken } from '../../../models/authorization.interface';

/**
 * Manages token refresh state and session invalidation to prevent concurrent refresh requests
 * and stop requests when session becomes invalid
 */
@Injectable({
  providedIn: 'root',
})
export class AuthStateService {
  private readonly refreshInProgress$ = new BehaviorSubject<boolean>(false);
  private readonly refreshResult$ =
    new BehaviorSubject<IResRefreshToken | null>(null);
  private readonly sessionInvalidated$ = new BehaviorSubject<boolean>(false);

  get isRefreshing(): boolean {
    return this.refreshInProgress$.value;
  }

  get isSessionInvalidated(): boolean {
    return this.sessionInvalidated$.value;
  }

  startRefresh(): void {
    this.refreshInProgress$.next(true);
    this.refreshResult$.next(null);
  }

  completeRefresh(result: IResRefreshToken): void {
    this.refreshResult$.next(result);
    this.refreshInProgress$.next(false);
  }

  failRefresh(): void {
    this.refreshResult$.next(null);
    this.refreshInProgress$.next(false);
  }

  waitForRefresh(): Observable<IResRefreshToken> {
    return this.refreshResult$.pipe(
      filter((result) => result !== null),
      take(1),
    );
  }

  invalidateSession(): void {
    this.sessionInvalidated$.next(true);
  }

  reset(): void {
    this.refreshInProgress$.next(false);
    this.refreshResult$.next(null);
    this.sessionInvalidated$.next(false);
  }
}
