import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, catchError, Observable, tap, throwError } from 'rxjs';
import {
  IFormLogout,
  IReqRefreshToken,
  IResAppToken,
  IResRefreshToken,
} from '../models/authorization.interface';
import { ResponseStatus } from '../models/response.interface';

@Injectable({
  providedIn: 'root',
})
export class LoginService {
  private readonly isLoggedInSubject = new BehaviorSubject<boolean>(false);
  isLoggedIn$ = this.isLoggedInSubject.asObservable();

  constructor(private readonly http: HttpClient) {}

  refreshToken(uamBaseApiUrl: string, clientId: string): Observable<any> {
    const url: string = uamBaseApiUrl + 'v1/appauth/refresh-token';
    const _httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'X-ClientId': clientId,
      }),
    };

    const payload: IReqRefreshToken = {
      SystemId: clientId,
    };
    return this.http.post<IResRefreshToken>(url, payload, _httpOptions);
  }

  verifyToken(
    uamBaseApiUrl: string,
    clientId: string,
  ): Observable<ResponseStatus<IResAppToken>> {
    return this.http
      .post<ResponseStatus<IResAppToken>>(
        uamBaseApiUrl + `v1/appauth/app-token`,
        {
          appId: clientId,
        },
      )
      .pipe(
        tap((res) => {
          return this.isLoggedInSubject.next(res.data.result.isValid);
        }),
        catchError((err) => {
          console.log(err);
          return throwError(() => err);
        }),
      );
  }

  checkAuth(): boolean {
    return this.isLoggedInSubject.getValue();
  }

  logout(body: IFormLogout, uamBaseApiUrl: string): Observable<unknown> {
    localStorage.setItem('logout-event', Date.now().toString());
    return this.http.post<unknown>(uamBaseApiUrl + `v1/appauth/logout`, body);
  }
}
