import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { ResponseObjectType } from '../models/response.interface';
import { IUserProfile } from '../models/user-profile.interface';

@Injectable({
  providedIn: 'root',
})
export class UserProfileService {
  private readonly isLoggedInSubject = new BehaviorSubject<boolean>(false);
  isLoggedIn$ = this.isLoggedInSubject.asObservable();

  constructor(private readonly http: HttpClient) {}

  getProfileUam(
    userName: string,
    clientId: string,
    uamBaseApiUrl: string,
  ): Observable<ResponseObjectType<IUserProfile>> {
    const data = {
      userName: userName,
      systemId: clientId,
    };
    return this.http.get<ResponseObjectType<IUserProfile>>(
      uamBaseApiUrl + 'v2/users/get-user-byUsername',
      { params: data },
    );
  }
}
