import { Injectable, inject } from '@angular/core';
import { HttpRequest, HttpEvent, HttpHandlerFn } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, switchMap } from 'rxjs/operators';
import { LoginService } from '../../login.service';
import { AuthStateService } from './auth-state.service';
import { ErrorModalService } from './error-modal.service';
import {
  IResRefreshToken,
  IUrlConfig,
} from '../../../models/authorization.interface';

/**
 * Handles token refresh logic with queueing for concurrent requests
 */
@Injectable({
  providedIn: 'root',
})
export class TokenRefreshService {
  private readonly loginService = inject(LoginService);
  private readonly authState = inject(AuthStateService);
  private readonly errorModal = inject(ErrorModalService);

  refreshAndRetry(
    req: HttpRequest<unknown>,
    next: HttpHandlerFn,
    urlConfig: IUrlConfig,
    clientId: string,
    applilcationStorageKey: string,
  ): Observable<HttpEvent<unknown>> {
    if (this.authState.isRefreshing) {
      // Wait for ongoing refresh to complete, then retry request
      return this.authState.waitForRefresh().pipe(
        switchMap(() => next(req)),
        catchError((err) => {
          this.errorModal.showSessionExpired(
            urlConfig.uamLoginURL,
            applilcationStorageKey,
          );
          return throwError(() => err);
        }),
      );
    }

    // Start new refresh process
    return this.performRefresh(
      req,
      next,
      urlConfig,
      clientId,
      applilcationStorageKey,
    );
  }

  private performRefresh(
    req: HttpRequest<unknown>,
    next: HttpHandlerFn,
    urlConfig: IUrlConfig,
    clientId: string,
    applilcationStorageKey: string,
  ): Observable<HttpEvent<unknown>> {
    this.authState.startRefresh();

    return this.loginService
      .refreshToken(urlConfig.uamBaseApiUrl, clientId)
      .pipe(
        switchMap((response: IResRefreshToken) => {
          this.authState.completeRefresh(response);
          return next(req);
        }),
        catchError((err) => {
          this.authState.failRefresh();
          this.errorModal.showSessionExpired(
            urlConfig.uamLoginURL,
            applilcationStorageKey,
          );
          return throwError(() => err);
        }),
      );
  }
}
